<template>
  <div class="container-fluid" style="minHeight:100vh;">
    <div class="row py-4 text-center grey-text">
      <span>Classifieds in Costa Rica</span>
    </div>
    <div class="row" v-if="arePosts">
      <div class="col s12 m5 l3" v-for="(post, index) in posts" :key="index">
        <div class="card">
          <div class="card-image" v-if="post.images">
            <img :src="post.images[0].url" />
            <span class="card-title">{{ post.title }}</span>
          </div>
          <div class="card-content">
            <p>
              {{
                post.description.length > 60
                  ? post.description.substring(0, 60) + "..."
                  : post.description
              }}
            </p>
            <br />
            <span class="mb-3" style="fontSize:1.2em;">â‚¡{{ post.price }}</span>
          </div>
          <div class="card-action">
            <router-link :to="{ name: 'product', params: { postId: post.id } }"
              >See more
            </router-link>
            <span class="badge">{{ getCategory(post.category) }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapMutations, mapState } from "vuex";
import firebase from "@firebase/app";
export default {
  name: "Posts",
  data() {
    return {
      db: null,
      // posts: [],
      categories: [
        {
          value: 1,
          name: "Art",
        },
        {
          value: 2,
          name: "Electronics",
        },
        {
          value: 3,
          name: "Vehicles",
        },
        {
          value: 3,
          name: "Sports",
        },
        {
          value: 4,
          name: "Real Estate",
        },
      ],
    };
  },
  computed: {
    ...mapState("posts", ["posts"]),
    arePosts() {
      return this.posts.length > 0;
    },
  },
  methods: {
    ...mapMutations(["startLoader", "endLoader"]),
    ...mapActions("posts", ["getPosts"]),

    getCategory(index) {
      return this.categories[index - 1].name;
    },
  },
  created() {
    this.getPosts();
    this.startLoader();
  },
  mounted() {
    setTimeout(() => {
      this.endLoader();
    }, 200);
  },
};
</script>

<style scoped>
.card {
  height: 430px !important;
}
.card-content {
  max-height: 130px;
  min-height: 130px;
}
.card-image .card-title {
  width: 100%;
  font-weight: 400;
  text-transform: capitalize;
  box-shadow: inset 0px -70px 20px -15px #4e4e4ebb;
}
.card-image img {
  max-height: 240px;
  min-height: 240px;
  object-fit: cover;
}
</style>
